<script>
  import $ from 'jquery'
  function showLastToast() {
    var toastElList = [].slice.call(document.querySelectorAll('.toast'))
    var toastList = toastElList.map(function (toastEl) {
        return new bootstrap.Toast(toastEl)
    })
    toastList[toastList.length - 1].show()
}
$('.offcanvas a').on('click',()=>{
  console.log('test')
  $('button[data-bs-dismiss=offcanvas]').click()
})
  if (!("Notification" in window)) {
    // Check if the browser supports notifications
    alert("This browser does not support desktop notification");
  } else if (Notification.permission === "granted") {
    // Check whether notification permissions have already been granted;
    // if so, create a notification
    const notification = new Notification("Hi there!");
    // …
  } else if (Notification.permission !== "denied") {
    // We need to ask the user for permission
    Notification.requestPermission().then((permission) => {
      // If the user accepts, let's create a notification
      if (permission === "granted") {
        const notification = new Notification("Hi there!");
        // …
      }
    });
  }
  export default {
    data() {
      return {
        cont: null,
        user: {
          name: '未登录',
          ava: 'res/default.png',
          uid: '',
        },

        isLogin: false,
        isAdmin: false
      }
    },
    methods: {
      getInfo(){
        fetch('/api/getUserInfo', {
          headers: { Token: localStorage.token }
        }).then(d => d.json()).then(d => {
          if (d.status == 200) {
            var un = d.data.alia === null ? d.data.username : d.data.alia
            this.user = {
              name: un,
              ava: d.data.avatar,
              uid: d.data.id
            }
            this.isLogin = d.guest == 0

            if (d.guest == 1)

              setTimeout(() => {
                $(`<div class="toast" role="alert" aria-live="assertive" aria-atomic="true" data-bs-animation="true" data-bs-autohide="false">
                        <div class="toast-header">
                            <img src="" class="rounded me-2" alt="">
                            <strong class="me-auto">提示</strong>
                            <button type="button" class="btn-close" data-bs-dismiss="toast" aria-label="Close"></button>
                        </div>
                        <div class="toast-body">
                            建议您登录到Diary Music 官网来体验官网的完整功能。
                            <div class="mt-2 pt-2 border-top">
                                <a href="#/login" class="me-3">前往登录</a>
                            </div>
                        </div>
                    </div>`).appendTo('.toast-container')
                showLastToast()
                var time = 0
                $('.time').text(time + '秒前');
                setInterval(() => {
                  time++
                  if (time > 59) {
                    $('.time').text(parseInt(time / 60) + '分钟前');
                  } else {
                    $('.time').text(time + '秒前');
                  }
                }, 1000);
              }, 1000);
          }
          else {
            localStorage.setItem('token', d.token)
            fetch('/api/getUserInfo', {
              headers: { Token: localStorage.token }
            }).then(d => d.json()).then(d => {
              if (d.status == 200)
                var un = d.data.alia === null ? d.data.username : d.data.alia
              this.user = {
                name: un,
                ava: d.data.avatar,
                uid: d.data.id
              }
              this.isLogin = d.guest == 0
            })
          }
        })
      },
      logout() {
        fetch('/api/logout').then(d => d.json()).then(d => localStorage.token = d.status == 200 ? d.token : undefined)


        location.hash = '/login'
        this.getInfo()
      },
    },
    created() {

    },
    mounted() {
      this.getInfo()
    },
  }
</script>
